/* components */
html {
  scroll-behavior: smooth;
}

body {
  font-family: "Manrope", sans-serif;
  color: #333;
  font-size: 1.125rem;
  overflow-x: hidden;
}

.container-fluid, .container-sm, .container-md, .container-lg, .container-xl, .container-xxl {
  max-width: 1400px;
}

a {
  transition: all 0.2s ease;
}

input[type=submit],
button {
  transition: all 0.2s ease;
}

img {
  display: block;
  max-width: 100%;
  height: auto;
}

figure.alignnone,
.alignnone > img,
img.alignnone {
  margin: 0px 25px 25px;
}

figure.aligncenter,
.aligncenter > img,
img.aligncenter {
  margin: 0px auto 25px;
}

figure.alignleft,
.alignleft > img,
img.alignleft {
  float: left;
  margin: 0px 25px 25px 0px;
}

figure.alignright .alignright > img,
img.alignright {
  float: right;
  margin: 0px 0px 25px 25px;
}

.wp-block-image.alignwide,
.wp-block-image.alignfull {
  margin: 0px !important;
}

.inline-image img {
  display: inline;
  margin-left: 10px;
}

.same-height div[class^=p-] {
  display: flex;
  align-items: flex-start;
  justify-content: center;
  height: 100%;
}
.same-height div[class^=p-] > * {
  height: 100%;
}

.wp-block {
  max-width: none;
}

.text-center {
  text-align: center;
}

.text-right,
.text-end {
  text-align: right;
}

.text-left,
.text-start {
  text-align: left;
}

.block-editor-block-list__layout.is-root-container {
  padding-left: 15px !important;
  padding-right: 15px !important;
  padding-bottom: 100px !important;
}

.wp-block-wp-bootstrap-blocks-container {
  max-width: 1400px;
  padding-left: 15px;
  padding-right: 15px;
  margin-left: auto;
  margin-right: auto;
}

.is-reusable > .alignfull {
  width: 100vw !important;
  left: 50%;
  margin-left: -50vw;
  max-width: none !important;
  position: relative;
}

.wp-block-cover.hero-section .block-editor-block-list__layout {
  align-items: center;
